import React from "react"
import Layout from "../components/layout"
import Seo from "../components/seo"
import worksData from "../data/worksData.json"

const WorkItem = ({ item }) => {
  const tags = item.tags ? item.tags.split(",") : []

  return (
    <div className="content-item">
      <small>{item.date}</small>
      <h3>{item.position}</h3>
      <h4>{item.company}</h4>
      <span>{item.country}</span>
      <div>
        {tags &&
          tags.map((tag, i) => (
            <span key={i} className="badge badge-danger">
              {tag}
            </span>
          ))}
      </div>
      {item.link && (
        <span>
          Oficial{" "}
          <a href={item.link} target="_blank" rel="noreferrer">
            {item.link}
          </a>
        </span>
      )}
      {item.developments && (
        <ul className="list-group">
          {item.developments.map((data, i) => (
            <li key={Date.now() + i}>
              <i className="fas fa-link"></i>{" "}
              {data.link ? (
                <span>
                  Project: <a href={data.link}>{data.name}</a>
                </span>
              ) : (
                <span>{data.name}</span>
              )}
            </li>
          ))}
        </ul>
      )}
    </div>
  )
}

const Portafolio = () => {
  // console.log({ worksData });
  return (
    <>
      <Seo title="Portafolio" />
      <Layout>
        <section className="section-wrapper section-experience gray-bg mt-5">
          <div className="container-fluid">
            <div className="row">
              <div className="col-md-12">
                <div className="section-title">
                  <h2>
                    <i className="fas fa-briefcase"></i> Portafolio
                  </h2>
                </div>
              </div>
              <div className="col-md-12">
                {worksData.map((workItem, i) => {
                  return (
                    workItem.type === "free" && (
                      <WorkItem item={workItem} key={i} />
                    )
                  )
                })}
              </div>
            </div>
          </div>
        </section>
      </Layout>
    </>
  )
}

export default Portafolio
